.event {
    text-align: center;
}

.head-text {
    position: relative;
    text-shadow: 1px 0 #fff, -1px 0 #fff, 0 1px #fff, 0 -1px #fff,
        1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
}

.finished {
    -webkit-filter: grayscale(100%) blur(0.2rem);
    -moz-filter: grayscale(100%) blur(0.2rem);
    -o-filter: grayscale(100%) blur(0.2rem);
    -ms-filter: grayscale(100%) blur(0.2rem);
    filter: grayscale(100%) blur(0.1rem);
}

.text-on-image {
    position: absolute;
    right: 50%;
    left: 50%;
    bottom: 15%;
}

.top-left-text {
    position: absolute;
    left: 3%;
    top: 3%;
}

.bottom-left-text {
    position: absolute;
    left: 3%;
    bottom: 3%;
}

.top-right-text {
    position: absolute;
    right: 3%;
    top: 3%;
}

.gallery-image {
    max-width: 300px;
    max-height: 300px;
}