.box {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    background-color: #f5f5f5;
    background-color: #e5ffe5;
    max-height: 20%;
    /* display: table;
    clear: both; */
    display: flex;
    overflow-x: scroll;
}

.event-text {
    height: 300px;
    width: 300px;
    justify-content: center;
    align-items: center;
    display: flex;
}

.box>div {
    /* display: table; */
    /* clear: both; */
}

.answered {
    background-color: #999999;
    filter: grayscale(50%);
}

/* 
.answered img {
    filter: grayscale(50%);
} */

.teamname {
    font-weight: bold;
    font-size: large;
}

.box .head-text img {
    max-width: 300px;
}

.box>* {
    margin: 0px 10px;
}

.head-text {
    width: fit-content;
    height: fit-content;
    position: relative;
    text-shadow: 1px 0 #fff, -1px 0 #fff, 0 1px #fff, 0 -1px #fff,
        1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
}

.top-left-text {
    position: absolute;
    left: 3%;
    top: 3%;
}

.bottom-left-text {
    position: absolute;
    left: 3%;
    bottom: 3%;
}

.compare-box {
    min-width: 300px;
    width: 300px;
    /* display: contents; */
    display: inline-block;
}

/* custom scrollbar */
::-webkit-scrollbar {
    width: 20px;
}

::-webkit-scrollbar-track {
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #a8bbbf;
}

.button {
    color: white;
    font-weight: "bold";
    font-size: larger;
}

.redbutton {
    background-color: #d9534f;
}

.event-text-box .description {
    color: #999999;
}

.event-box {
    background-color: bisque;
}

.event-text-box {
    height: 300px;
    width: 300px;
    display: block;
}

.event-text-box>* {
    display: block;
    margin-top: 20px;
}

.event-text-box>*:first-child {
    margin-top: 0px;
}